*,
*::after,
*::before {
  box-sizing: border-box;
}
p {
  margin: 0;
}
.page {
  margin: 0;
  background-color: #f4f4f6;
}
.page__wrapper {
  width: 100%;
  min-height: 696px;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 26px 0 #e1e1e1;
}
@media (min-width: 1300px) {
  .page__wrapper {
    max-width: 1300px;
    margin-top: 114px;
    margin-bottom: 139px;
  }
}
.header {
  display: flex;
  align-items: center;
  padding-top: 15px;
  padding-left: 3.7%;
  padding-right: 3.7%;
  background-color: #ffffff;
}
.header__author {
  text-transform: uppercase;
}
.header__time {
  margin-left: 15px;
}
.header__avatar {
  margin-left: 15px;
}
.header__access {
  display: none;
  margin-left: 8px;
}
@media (min-width: 1300px) {
  .header {
    padding-top: 30px;
    padding-left: 45px;
    padding-right: 45px;
    border-radius: 8px 8px 0 0;
  }
  .header__access {
    display: block;
  }
}
.main {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}
.main__header {
  min-height: 446px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.main__header::after {
  content: "";
  width: auto;
  height: 220px;
  background-image: url("../img/gift.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center center;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .main__header::after {
    background-image: url("../img/rectangle-copy-25@2x.jpg");
  }
}
.main__title {
  margin-top: 16px;
  margin-left: 3.7%;
  margin-right: 3.7%;
  margin-bottom: 0;
}
.main__text {
  margin-top: 8px;
  margin-left: 3.7%;
  margin-right: 3.7%;
}
@media (min-width: 1300px) {
  .main__header {
    min-height: 589px;
  }
  .main__header::after {
    content: "";
    width: 1300px;
    height: 450px;
    background-image: url("../img/rectangle-copy-25.jpg");
  }
  .main__title {
    margin-left: 45px;
    margin-right: 45px;
  }
  .main__text {
    width: 485px;
    margin-left: 45px;
    margin-right: 45px;
    margin-bottom: 30px;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .main__header::after {
    background-image: url("../img/rectangle-copy-25@2x.jpg");
  }
}
.block {
  background-color: #142536;
  border-bottom: solid 2px #22374b;
}
.block__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  padding-left: 4%;
  padding-right: 4%;
  padding-bottom: 15px;
  cursor: pointer;
}
.block__header::after {
  content: "";
  width: 14px;
  height: 6px;
  background-image: url("../img/path-2-copy.svg");
  background-repeat: no-repeat;
}
.block__header:hover {
  background-color: #22374b;
}
.active::after {
  background-image: url("../img/path-2-copy-7.svg");
}
.block__title {
  width: 300px;
  min-height: 84px;
}
.block__content {
  position: relative;
  display: flex;
  flex-direction: column;
  max-height: 0;
  padding-left: 4%;
  padding-right: 4%;
  overflow: hidden;
}
.block__content::before {
  content: "";
  position: absolute;
  width: 92%;
  height: 1px;
  background-color: #405265;
}
.block__text {
  margin-top: 15px;
}
.block__image {
  display: block;
  width: 250px;
  height: 250px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
  border-radius: 50%;
}
@media (min-width: 1300px) {
  .block--bottom {
    border-radius: 0 0 8px 8px;
  }
  .block__header {
    padding-top: 30px;
    padding-left: 45px;
    padding-right: 45px;
    padding-bottom: 30px;
  }
  .block__title {
    width: auto;
    min-height: 28px;
  }
  .block__content {
    flex-direction: row;
    flex-wrap: wrap;
    padding-left: 45px;
    padding-right: 45px;
  }
  .block__text {
    max-width: 693px;
    margin-top: 30px;
  }
  .block__image {
    margin-top: 30px;
    margin-left: 111px;
    margin-right: 149px;
    margin-bottom: 30px;
  }
  .block__form {
    margin-top: -150px;
  }
}
.form {
  display: flex;
  flex-direction: column;
}
.form__wrapper {
  position: relative;
}
.form__input {
  width: 344px;
  height: 30px;
  margin-top: 0;
  margin-bottom: 30px;
  background-color: transparent;
  border: none;
  border-bottom: solid 1px #5c738b;
}
.form__input::placeholder {
  font-family: "ALSStory", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: #ffffff;
}
.form__input:hover {
  border-bottom: solid 1px #ffffff;
}
.form__input:hover + .form__clear {
  display: inline-block;
}
.form__clear {
  position: absolute;
  top: 0;
  left: 330px;
  display: none;
  width: 14px;
  height: 14px;
  background-color: transparent;
  border: none;
  background-image: url("../img/clear.svg");
  cursor: pointer;
}
.form__clear:hover {
  display: inline-block;
}
.form__submit {
  width: 342px;
  min-height: 50px;
  margin-top: 14px;
  margin-bottom: 30px;
  background-color: #142536;
  border: solid 1px #ffffff;
  border-radius: 25px;
  cursor: pointer;
}
@media (min-width: 1300px) {
  .form__input {
    width: 281px;
  }
  .form__clear {
    left: 260px;
  }
  .form__submit {
    width: 218px;
  }
}
